.mainCarousel {
  list-style: none;
}

.img {
  height: 100%;
  /* width: 100%; */
  object-fit: cover;
  border: 3px solid blue;
  border-radius: 10px;
}

.slider {
  overflow: hidden;
  /* height: 100vh; */
  width: 80vw;
  /* margin: 0 auto; */
}
@media (min-width: 605px) {
  .img {
    width: 50vw;
  }
  .slider {
    width: 50vw;
  }
}
.slider figure div {
  width: 20%;
  height: 100%;
  float: right;
}

.slider figure img {
  width: 90%;
  float: right;
}

.slider figure {
  position: relative;
  width: 500%;
  margin: 0;
  /* right: 0; */
  -webkit-animation: 40s slidy infinite;
          animation: 40s slidy infinite;
}

@-webkit-keyframes slidy {
  0% {
    left: 0%;
  }
  10% {
    left: 0%;
  }
  12% {
    left: -100%;
  }
  22% {
    left: -100%;
  }
  24% {
    left: -200%;
  }
  34% {
    left: -200%;
  }
  36% {
    left: -300%;
  }
  46% {
    left: -300%;
  }
  48% {
    left: -400%;
  }
  58% {
    left: -400%;
  }
  60% {
    left: -300%;
  }
  70% {
    left: -300%;
  }
  72% {
    left: -200%;
  }
  82% {
    left: -200%;
  }
  84% {
    left: -100%;
  }
  94% {
    left: -100%;
  }
  96% {
    left: 0%;
  }
}

@keyframes slidy {
  0% {
    left: 0%;
  }
  10% {
    left: 0%;
  }
  12% {
    left: -100%;
  }
  22% {
    left: -100%;
  }
  24% {
    left: -200%;
  }
  34% {
    left: -200%;
  }
  36% {
    left: -300%;
  }
  46% {
    left: -300%;
  }
  48% {
    left: -400%;
  }
  58% {
    left: -400%;
  }
  60% {
    left: -300%;
  }
  70% {
    left: -300%;
  }
  72% {
    left: -200%;
  }
  82% {
    left: -200%;
  }
  84% {
    left: -100%;
  }
  94% {
    left: -100%;
  }
  96% {
    left: 0%;
  }
}

